.overlay {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.65);
  z-index: 100;
  overflow-y: auto; }
  @media (min-width: 992px) {
    .overlay {
      align-items: center; } }

.modal {
  position: relative;
  flex: 1;
  margin: 6.25rem 0; }
  @media (min-width: 992px) {
    .modal {
      margin: 2.5rem 0; } }

.modal-content {
  background-color: #fff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 2px 2px 32px rgba(0, 0, 0, 0.8);
  width: 90vw;
  min-height: 90vh; }
  @media (min-width: 992px) {
    .modal-content {
      max-width: 53.125rem;
      min-height: 50vh; } }

.modal__close {
  position: absolute;
  bottom: 100%;
  right: 0;
  margin-bottom: 0.75rem;
  transition: transform .35s ease-out; }
  @media (min-width: 992px) {
    .modal__close {
      margin-left: 1.5rem;
      left: 100%; } }
  .modal__close:hover, .modal__close:focus {
    transform: scale(1.15) rotate(15deg); }



