.header {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 19.6875rem;
  background-color: blue; }

.selfie-wrapper {
  text-align: center;
  padding: 5rem 0 3.125rem; }

.selfie {
  width: 16.25rem;
  height: 16.25rem;
  border-radius: 50%; }

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 0 2.5rem; }

.attributes {
  width: 100%;
  margin: 2.5rem 0 1.875rem;
  padding-top: 1.875rem;
  text-align: left;
  border-top: 4px solid #EAEAEA; }

.attributes__item {
  margin-bottom: 3.75rem; }

.attributes__key {
  color: #67717E;
  font-size: 1.375rem;
  font-weight: normal; }

.attributes__value {
  color: inherit;
  text-transform: uppercase;
  font-weight: bolder; }



