.sidebar {
  flex: 1 100%;
  background-color: #F5F5F5; }
  @media (min-width: 768px) {
    .sidebar {
      flex: 0 30rem;
      width: 30rem;
      height: 100%;
      min-height: calc(100vh - 8.125rem);
      overflow-y: auto; } }

.sidebar__list {
  list-style-type: none;
  padding: 0;
  margin: 0; }

.sidebar__link {
  display: flex;
  background-color: #EAEAEA;
  color: inherit;
  padding: 2.5rem; }



