.header {
  display: flex;
  align-items: center;
  background-color: #F5F5F5;
  border-bottom: 1px solid #EAEAEA;
  height: 8.125rem; }

.logo-wrapper {
  display: flex;
  justify-content: center;
  width: 30rem; }

.right-wrapper {
  display: flex;
  align-items: center;
  margin-left: auto;
  height: 100%; }

.profile-img {
  width: 4.6875rem;
  height: 4.6875rem; }

.profile-link {
  padding: 0 3.25rem; }

.connected-button {
  display: flex;
  align-items: center;
  height: 100%;
  background-color: #D5ECFF;
  padding: 0 1.625rem; }

.connected-button-inner {
  display: flex;
  align-items: baseline; }



