.activity-list {
  list-style-type: none;
  padding: 0;
  margin: 0; }

.activity-item {
  border: 1px solid #EAEAEA; }

.activity__link {
  display: block;
  transition: box-shadow .1s ease-out; }
  .activity__link:hover, .activity__link:focus {
    text-decoration: none;
    box-shadow: 1px 1px 32px rgba(0, 0, 0, 0.15); }

.activity__header {
  padding: 1.875rem 2.5rem;
  border-bottom: 1px solid #EAEAEA;
  background-color: #F5F5F5;
  color: #67717E; }

.activity__content {
  display: flex;
  align-items: center;
  padding: 1.875rem 2.5rem;
  color: #333B40; }

.activity__date {
  margin-left: auto;
  text-align: right;
  font-size: 1.25rem;
  color: #09D2B0;
  line-height: 1.5; }



